import React from "react";
import _ from "underscore";
import $ from "jquery";
import "./JobHot.css";
import { JobListSmall } from "Components/Job";

const JobHot = () => {
  const locationData = [
    {
      city: "Thnh ph H Ch Minh",
      isActive: true,
    },
    {
      city: "Qun 1",
      isActive: false,
    },
    {
      city: "Qun 2",
      isActive: false,
    },
    {
      city: "Qun 3",
      isActive: false,
    },
    {
      city: "Qun 4",
      isActive: false,
    },
    {
      city: "Qun 5",
      isActive: false,
    },
    {
      city: "Qun 6",
      isActive: false,
    },
    {
      city: "Qun 7",
      isActive: false,
    },
    {
      city: "Qun 8",
      isActive: false,
    },
    {
      city: "Qun 9",
      isActive: false,
    },
    {
      city: "Qun 10",
      isActive: false,
    },
    {
      city: "Qun 11",
      isActive: false,
    },
    {
      city: "Qun 12",
      isActive: false,
    },
    {
      city: "Th c",
      isActive: false,
    },
    {
      city: "G Vp",
      isActive: false,
    },
    {
      city: "Bnh Thnh",
      isActive: false,
    },
    {
      city: "Tn Bnh",
      isActive: false,
    },
    {
      city: "Tn Ph",
      isActive: false,
    },
    {
      city: "Ph Nhun",
      isActive: false,
    },
    {
      city: "Bnh Tn",
      isActive: false,
    },
    {
      city: "C Chi",
      isActive: false,
    },
    {
      city: "Hc Mn",
      isActive: false,
    },
    {
      city: "Bnh Chnh",
      isActive: false,
    },
    {
      city: "Nh B",
      isActive: false,
    },
    {
      city: "Cn Gi",
      isActive: false,
    },
  ];

  const scrollLocation = (direction) => () => {
    let far = ($(".JobHot__list-location").width() / 6) * direction;
    let pos = $(".JobHot__list-location").scrollLeft() + far;
    $(".JobHot__list-location").animate({ scrollLeft: pos }, 500);
  };

  return (
    <div className="JobHot__container jh-container mt-3">
      <div className="JobHot__box-smart-box">
        <div className="JobHot__box-label">
          <p>
            <div className="JobHot__dot" />
             xut bi JobHere AI
          </p>
        </div>
      </div>
      <div className="JobHot__box-header d-flex">
        <h2>Tin tuyn dng, vic lm tt nht</h2>
        <span class="pull-right feature-job-link">
          <a
            href="https://www.topcv.vn/viec-lam-tot-nhat"
            target="_blank"
            rel="noreferrer"
          >
            Xem tt c <i className="bi bi-arrow-right" />
          </a>
        </span>
      </div>
      <div className="JobHot__box-smart-filter">
        <div className="JobHot__box-smart-location d-flex align-items-center">
          <div className="JobHot__prev-location me-2">
            <a onClick={scrollLocation(-1)}>
              <i className="bi bi-chevron-left" />
            </a>
          </div>
          <div className="JobHot__list-location">
            {_.map(locationData, (item, index) => {
              return (
                <div
                  key={index}
                  className={
                    item.isActive
                      ? "JobHot__item-location-active"
                      : "JobHot__item-location"
                  }
                >
                  {item.city}
                </div>
              );
            })}
          </div>
          <div className="JobHot__next-location ms-2">
            <a onClick={scrollLocation(1)}>
              <i className="bi bi-chevron-right" />
            </a>
          </div>
        </div>
      </div>
      <div className="JobHot__list-job">
        <JobListSmall />
      </div>
    </div>
  );
};

export default JobHot;
